.cart-div{
    height: 100vh;
    width: 30vw;
    background-color: rgba(0, 0, 0, 0.9);
    position: fixed;
    right: 0;
    top: 0;
    z-index: 1;
}

.close-menu p{
    text-align: end;
    margin: 0;
    margin-right: 50px;
    font-size: 3em;
}

.cart-div p{
    word-wrap: break-word;
    color: white;
}

.list-of-pizzas p{
    margin: 50px 25px;
}

.cart-div img{
    width: 200px;
}