:root{
    --header-height: 60px;
}

header{
    height: var(--header-height);
    background-color: white;
    box-shadow: 0 1px 10px rgba(0, 0, 0, 0.228);
}

nav{
    height: var(--header-height);
    display: flex;
    justify-content: center;
    gap: 100px;
}

nav > .logo-img{
    margin: 1px 1px 1px 1px;
}

nav .create-button{
    display: flex;
    justify-content: space-around;
}

nav .create-button > button {
    width: 150px;
    background-color: white;
    border: 1px solid rgb(223, 223, 223);
    transition: all .1s;
}

nav .create-button > button:hover {
    background-color: rgb(244, 244, 244);
    transform: scale(1.02);
}

nav .create-button > button img{
    width: 30px;
    height: 30px;
    margin-right: 10px;
    margin: auto;
}

nav .create-button > button div{
    display: flex;
    justify-content: center;
}

nav .create-button > button p{
    font-size: 1.1em;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 400;
    width: 70px;
    margin: 0;
}

main div > h1 {
    text-align: center;
}

nav .cart-button button{
    width: 150px;
    background-color: rgb(255, 0, 0);
    border: 1px ridge rgba(0, 0, 0, 0.4);
    transition: .35s;
    height: var(--header-height);
    color: white;
    transition: all .1s;
}
nav .cart-button button:hover{
    background-color: rgb(194, 31, 31);
    transform: scale(1.02);
}
nav .cart-button button div{
    display: inline;
    background-color: rgb(123, 0, 2);
    border-radius: 150px;
    padding: 2px;
    margin-right: 3px;
    box-shadow: 0 1.5px 3px 1px rgb(0, 0, 0, 0.4);
}
nav .cart-button button p{
    display: inline;
}
nav .cart-button button div p{
    font-weight: 700;
}

.menu img{
    width: 60px;
}
.menu{
    display: none;
}

@media (max-width: 800px)
{
    nav{
        justify-content: space-between;
    }

    nav > .logo-img, .menu{
        margin: 1px 30px;
    }

    .create-button button, .cart-button{
        display: none;
    }

    .menu{
        display: inline;
    }
}